export function debug(msg, label = 'superv', params) {

  if (process.env.NODE_ENV !== 'development') {
    return
  }
  label = label.toLowerCase()

  const hide = []
  if (hide.find(hd => hd === label)) {
    return
  }

  const fontSize = '0.7rem'
  const padSize = '.25rem 1rem'

  const colorMap = {
    default: { bg: 'indianred' },
    superv: { bg: 'green' },
    event: { bg: 'deeppink' },
    router: { bg: 'dodgerblue' },
    root: { bg: 'aquamarine', txt: 'black' },
    api: { bg: 'MediumTurquoise', txt: 'black' },
    auth: { bg: 'MediumSeaGreen', txt: 'black' },
    store: { bg: 'LightSlateGrey', txt: 'black' }

  }

  const box = function (bg, txt = 'black', pad = padSize) {
    return `background: ${bg}; color:${txt}; padding: ${pad}; font-size: ${fontSize};`
  }

  const boxFor = function (label) {
    const color = colorMap[label] || colorMap['default']
    return box(color.bg, color.txt)
  }

  if (label === 'eventfire') {
    console.log('%c EVENT >> ' + msg.toUpperCase(), box('DarkOrchid', 'white', '.25rem 1rem .25rem 1rem'))
    if (false && params) {
      console.log('%c PARAMS >> ' + JSON.stringify(params), box('white', 'DarkOrchid', '.7rem 1rem .7rem 10rem'))
    }
  } else {
    console.log('%c [ ' + label.toUpperCase() + ' ] %c ' + msg.toUpperCase(), boxFor(label) + 'padding-right:4rem;', box('bisque', 'black'))
  }
}

export function wait(ms) {
  return x => {
    return new Promise(resolve => setTimeout(() => resolve(x), ms))
  }
}

