<script>
import { Bar } from 'vue-chartjs'

export default {
  name: 'SvBarChart',
  props: ['data', 'chartOptions', 'width', 'height'],
  extends: Bar,
  data() {
    return {
      options: {
        responsive: true,
        scales: {
          xAxes: [{
            ticks: {
              maxRotation: 90,
              minRotation: 80
            }
          }],
          yAxes: [{
            ticks: {
              beginAtZero: true
            }
          }]
        }
      }
    }
  },
  mounted() {
    this.renderChart(this.data, this.options)
  },
}
</script>
