<script>
import { Doughnut } from 'vue-chartjs'

export default {
  name: 'SvLineChart',
  extends: Doughnut,
  props: ['data', 'chartOptions', 'width', 'height'],
  mounted() {
    const options = {
      legend: {
        display: true,
        position: 'right',
        labels: {
          boxWidth: 20,
        },
      },
      scales: {
        yAxes: [
          {
            display: false,
          },
        ],
      },
    }

    this.renderChart(this.data, { ...this.chartOptions, ...options })
  },
}
</script>
