<template>
  <tbody>
    <tr v-for="row in table.rows" :class="{'selected': table.selected.find(item => item.id === row.id)}">
      <td class="w-8" v-if="table.selectable">
        <input type="checkbox" :value="row" v-model="table.selected">
      </td>
      <td  v-if="table.showIdColumn">
       {{ row.id }}
      </td>
      <td v-for="field in row.fields">
        <sv-field
            :field="field"
            v-model="formData && formData[row.id].data[field.name]"
        ></sv-field>
      </td>
      <td class="center" style="width: auto" v-if="table.rowActions.length">
        <div class="flex flex-row-reverse  justify-start items-center w-full text-right row-actions">
          <slot   name="actions" :row="row">
            <template v-for="action in getRowActions(row)">
              <component
                  :is="action.component || 'sv-button'"
                  :key="action.id"
                  v-bind="makeProps(action, row)"
              ></component>
            </template>
          </slot>
<!--          <sv-icon name="more-vert" class="text-3xl text-gray-400 mx-3  px-4 "></sv-icon>-->
        </div>
      </td>
    </tr>
  </tbody>
</template>

<script>
import SvField from '../form/fields/SvField'
import SvIcon from '../SvIcon'

export default {
  name: 'SvTableBody',
  components: { SvIcon, SvField },
  props: ['refresh', 'form-data'],
  inject: ['table'],
  methods: {

    /**
     * Replace entry id, make reload function, insert row id
     *
     * @param action
     * @param row
     * @returns {}
     */
    makeProps(action, row) {
      const props = { ...action.props } || {}

      if (props['on-complete'] === 'reload') {
        props['on-complete'] = () => { this.table.reload() }
      }
      if (props['url']) {
        props['url'] = props['url'].replace('{entry.id}', row.id)
      }
      props.rowId = row.id

      return  props
    },

    /**
     * Get table row actions
     *
     * @param row
     * @returns []
     */
    getRowActions(row) {
      if (this.table.rowActions.length) {
        return this.table.rowActions
      }
      return row.actions || []
    },
  }
}
</script>

