<template>
  <component v-if="ready" v-bind="subprops" :is="matchedRoute.component"></component>
</template>
<script>
export default {
  name: 'SvPortal',
  props: {
    url: { type: String },
    subprops: { type: Object, defaut() {return {}} }
  },
  data() {
    return {
      ready: false,
      matchedRoute: {},
    }
  },

  watch: {
    url: {
      immediate: true,
      handler: function (url) {
        this.push(url)
      }
    }
  },
  methods: {
    push(url) {
      this.matchedRoute = this.$router.match('/' + url)
      if (this.matchedRoute) {
        this.$nextTick(() => this.ready = true)
      } else {
        this.$notifyError('Route not found for url: ' + this.url)
      }
    },
  },
}
</script>
