<template>
    <div class="card">
      <div class="header">
        <h2><strong>{{title}}</strong></h2>
      </div>
      <div class="body">
        <sv-line-chart v-if="type === 'line'"
                       :data="data"
                       :chart-options="options"
        ></sv-line-chart>
        <sv-pie-chart v-if="type === 'pie'"
                      :data="data"
                      :chart-options="options"
        ></sv-pie-chart>
        <sv-bar-chart v-if="type === 'bar'"
                      :data="data"
                      :chart-options="options"
        ></sv-bar-chart>
      </div>
    </div>
</template>
<style scoped>
  .card {
    background: #fff;
    border-radius: .55rem;
    margin-bottom: 30px;
    transition: .5s;
    border: 0;
    position: relative;
    width: 100%;
    box-shadow: 0 1px 4px 0 rgba(0,0,0,0.1)
  }

  .card .header {
    color: #424242;
    padding: 20px;
    position: relative;
    box-shadow: none;
  }



  .card .body {
    font-weight: 400;
    color: #424242;
    padding: 20px;
  }
</style>
<script>
import SvLineChart from './SvLineChart'
import SvPieChart from './SvPieChart'
import SvBarChart from './SvBarChart'

export default {
  name: 'SvChart',
  components: { SvBarChart, SvLineChart, SvPieChart },
  props: ['title', 'type', 'data', 'chartOptions'],
  computed: {
    options() {
      const defaultOptions = {
        legend: {
          display: false,
        },
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          xAxes: [
            {
              display: false,
            },
          ],
          yAxes: [
            {
              display: true,
            },
          ],
        },
      }
      return { ...defaultOptions }
    },
  },
}
</script>
