<script>

import { buildUrl } from '../lib/utils'

export default {
  name: 'SvLoader',
  props: ['tab', 'tab-id', 'request', 'url', 'listenEvent'],

  data() {
    return {
      block: {},
      ready: false,
      previous: {},
      filters: {},
    }
  },

  created() {
    if (this.listenEvent) {
      this.$events.listen(this.listenEvent, (filters) => {
        this.filters = filters
        this.fetch()
      })
    }
    this.fetch()
  },

  methods: {

    async fetch() {
      this.ready = false
      const url = this.makeUrl()
      const resp = await this.$api.get(url)
      if (resp.fail) {
        console.log('Cant fetch block', resp, this.block)
        return this.$notifyError(resp)
      }
      this.previous = this.block
      this.block = resp.data
      this.$nextTick(() => this.ready = true)

    },

    makeUrl() {
      if (this.filters.length) {
        return this.url + '?' + buildUrl(this.filters)
      }

      if (this.request) {
        return this.url + '?' + buildUrl(this.request)
      }
      return this.url
    },

    reload() { this.fetch() }
  },

  render(c) {
    if (!this.ready) {
      return null
    }

    const block = this.block

    const loader = this
    return c(block.component, {
      tag: 'component',
      props: { ...block.props, reload: loader.reload, tab: this.tab },
      on: {
        'page-title': (title) => {
          this.$emit('page-title', title)
        }
      }
    })

    return c(
      'div',
      {
        staticClass: 'w-full',
        on: {
          'click': ($event) => {
            if (!$event.altKey) {
              return null
            }
            this.reload()
          },
          'page-title': (title) => {
            this.$emit('page-title', title)
          }
        }
      },
      [component],
      1,
    )

  },

  render_old(c) {
    if (!this.ready) {
      return null
    }
    const loader = this
    let component = c('sv-block', {
      tag: 'component',
      props: { ...this.block, reload: loader.reload },
    })

    return c(
      'div',
      {
        staticClass: 'w-full',
        on: {
          'click': ($event) => {
            if (!$event.altKey) {
              return null
            }
            this.reload()
          },
        }
      },
      [component],
      1,
    )

  }
}
</script>
