<template>
  <div class="card-slim shadow-1 relative h-full">
    <!--<sv-icon name="more" class="absolute  text-3xl text-gray-500" style="margin-top: -2rem; right: 2px"></sv-icon>-->
    <div class="sv-overlay absolute" v-if="table.loading">
      <sv-loading-block></sv-loading-block>
    </div>

    <div class="card-header" v-if="config.title">
      <h2>{{config.title}}</h2>
    </div>

    <sv-table-actions  v-if="table.selected.length"></sv-table-actions>
    <div class="flex justify-between p-4">
      <div class="w-2/3">
        <sv-table-filters></sv-table-filters>
      </div>
      <div class="w-1/2 flex justify-end">
        <sv-block :key="action.uuid" v-bind="action" v-for="action in table.contextActions"></sv-block>
      </div>
    </div>

    <div class="overflow-hidden overflow-x-auto relative">
      <div class="px-8 py-4 flex items-center justify-center border-t border-gray-300"
           style="height: 200px;" v-if="!table.rows.length">
        <p v-if="table.ready">No results found</p>
      </div>
      <table border="0" cellpadding="0"
             cellspacing="0"
             class="table w-full"
             v-else
      >
        <sv-table-head></sv-table-head>
        <sv-table-body></sv-table-body>
      </table>
    </div>
    <div class="card-footer">
      <sv-pagination v-if="table.hasPagination"></sv-pagination>
    </div>

  </div>
</template>

<script>
import SvPagination from './SvPagination'
import SvTableFilters from './SvTableFilters'
import SvTableBody from './SvTableBody'
import SvTableActions from './SvTableActions'

import { saveAs } from 'file-saver/FileSaver'
import SvLoadingBlock from '../SvLoadingBlock'
import TableModel from './TableModel'
import SvIcon from '../SvIcon'
import SvTableHead from './SvTableHead'

export default {
  name: 'SvTable',
  components: {
    SvTableHead,
    SvIcon,
    SvLoadingBlock,
    SvTableActions,
    SvTableBody,
    SvTableFilters,
    SvPagination,
  },
  props: ['config'],
  data() {
    return {
      table: new TableModel(this.config, this.$superv),
    }
  },

  provide() {
    return { table: this.table }
  },
  created() {
    this.table.init()
    this.fetch()
  },

  methods: {
    async fetch() {
      const resp = await this.table.fetch()
      if (resp.fail) {
        this.$notifyError(resp)
      }
    },
    reload() {
      this.table.reload()
    },
    async exports() {
      const resp = await this.$api.post(
        this.dataUrl.replace('data', 'actions/export'),
        {},
        {
          responseType: 'blob',
        },
      )

      saveAs(
        new Blob([resp.$data]),
        'clients' +
          new Date()
            .toLocaleDateString()
            .split('/')
            .join('.') +
          '.xlsx',
      )
    },
    async debug() {
      window.open(this.config.data_url + '?user=root')
    },
  },
}
</script>


<style scoped>
.sv-overlay {
  background-color: rgba(0, 0, 0, 0.3) !important;
}
</style>
