<template>
  <div>
    <div v-if="presenting" v-html="value">
<!--      <sv-modal-button :content="content">Click here to view contents</sv-modal-button>-->
    </div>
    <div v-else>
      <quill-editor :options="editorOption"
                    ref="myQuillEditor"
                    v-if="rich"
                    v-model="input"
      >
      </quill-editor>
      <textarea class="sv-input" v-else v-model="input"></textarea>

    </div>
