export default {
  data() {
    return {
      form: {
        email: null,
        password: null,
      },
      failed: false,
      remember: false,
    }
  },
  created() {
    this.$events.listen({
      'login:success': (token) => {
        this.$notifySuccess('Login successful')
        if (!this.form.remember) {
          sessionStorage.setItem(this.tokenName, token)
        }
        this.$router.home()
      },
      'login:failed': (error) => {
        this.$notifyError(error)
      },
    })
  },
  methods: {
    login() {
      this.$auth.attempt({
        email: this.form.email,
        password: this.form.password,
      })
    },
  },
}