<template>
  <div class="sv-page-header">
    <div class="flex items-center pt-2" style="margin-left: 0;">
      <a class="mr-3 text-gray-400 hover:text-blue-400" @click="redirectToIndex">
        <sv-icon name="arrow-left" class="font-bold"></sv-icon>
      </a>
      <h1 class="header" style="color: #fff !important;">{{ title }}</h1>
    </div>
    <div class="flex items-center mr-12 mt-2">
      <slot name="actions">
        <sv-block
            v-for="action in actions"
            :key="action.uuid"
            v-bind="hydrate(action)"
        >
        </sv-block>
      </slot>
    </div>
  </div>
</template>

<script>
import SvIcon from '../components/SvIcon'

export default {
  name: 'SvPageHader',
  components: { SvIcon },
  props: ['title', 'actions', 'links'],
  data() {
    return {}
  },
  computed: {
    page() {
      return this.$parent.$props
    },
  },
  methods: {
    hydrate(action) {
      if (!action.props) {
        action.props = {}
      }
      if (
        action.props['on-complete'] &&
        action.props['on-complete'] === 'reload'
      ) {
        action.props['on-complete'] = this.reload
      }

      return action
    },
    redirectToIndex() {
      if (this.links) {
        this.$router.push(this.links.index)
      } else {
        this.$router.back()
      }
    },
  },
}
</script>

<style>
  .sv-page-header {
    @apply w-full flex justify-between items-center pt-4 px-8 pb-24 bg-cyan;
  }
</style>
