<template>
  <div :class="classList">
    <div class="w-full text-center" v-if="presenting">
      <div class="sv-select--presenter bg-grey-lighter text-gray-700">
        {{ value }}
      </div>
    </div>
    <div class="relative" v-else>
      <select :class="[input && !multiple ? 'mr-8' : 'mr-0', multiple ? 'h-48': '']"
              @change="changed"
              class="sv-select"
              :required="!multiple"
              :multiple="multiple"
              v-model="input">
        <option v-if="!multiple" :value="null" disabled>{{ placeholder }}</option>
        <option v-for="option in options"
                :value="option.value"
                :disabled="option.value === null"
                :selected="option.value === null && selected === null"
        >{{option.text}}
        </option>
      </select>

      <div v-if="!multiple && !input" :class="input ? 'mr-2' : 'mr-0'" class="sv-select-caret">
        <svg class="fill-current h-4 w-4" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
          <path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z"></path>
        </svg>
      </div>
      <a @click.prevent="input = null" class="sv-select--reset"
         v-if="!!input && !multiple">
        &times;
      </a>

    </div>
  </div>
</template>

<script>
import BaseField from './BaseField.js'
import { parseHttpQuery } from '../../../lib/utils'

export default {
  name: 'SvSelectField',
  mixins: [BaseField],
  data() {
    return {
      options: [],
    }
  },
  computed: {
    selected() {
      return this.input || null
    },
  },
  created() {
    if (this.meta.options) {
      const options = this.meta.options
      if (options.constructor === Array) {
        this.options = options
      } else if (options.constructor === Object) {
        this.options = this.formatOptions(options)
      } else if (options.constructor === String) {
        if (this.meta.autofetch !== false) {
          this.fetchOptions(this.meta.query)
        }
      }
    }

    if (this.meta.listen_event) {
      this.$events.listen(this.meta.listen_event, query => {
        this.fetchOptions(query)
      })
    }
  },
  mounted() {
    if (this.meta.on_change_event) {
      this.dispatchOnChangeEvent()
    }
  },
  methods: {
    async fetchOptions(query = {}) {
      const resp = await this.$api.post(this.meta.options, { query })
      if (resp.fail) {
        return this.$notifyError(resp)
      }
      this.options = resp.$data
    },
    formatOptions(options) {
      return Object.keys(options).map(key => {
        return { text: options[key], value: key }
      })
    },
    changed() {
      if (this.meta.on_change_event) {
        this.dispatchOnChangeEvent()
      }
    },

    dispatchOnChangeEvent() {
      const parts = this.meta.on_change_event.split(':')
      if (parts[1]) {
        parts[1] = parts[1].replace('{value}', this.input)
      }
      this.$events.fire(parts[0], parseHttpQuery(parts[1]))
    }
  },
}
</script>

<style>
.sv-select--presenter {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 0.75rem;
  text-align: center;
  padding-left: 2.5rem;
  padding-right: 2.5rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  width: auto;
  border-radius: 0.5rem;
  display: inline-block;
}

.sv-select {
  -webkit-appearance: none;
  -moz-appearance: none;
  outline: 0;
  width: 100%;
  border-radius: 0.25rem;
  display: inline-block;
  background-color: #f3f6fa;
  color: #1b476f;
  padding: 1rem 3rem 1rem 1rem;
}

select[disabled],
select:disabled,
select:invalid {
  color: rgba(27, 71, 111, 0.51);
}

.sv-select-caret {
  @apply  text-gray-500;
  pointer-events: none;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  display: flex;
  align-items: center;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

.sv-select--reset {
  @apply flex text-gray-500 bg-gray-300 rounded m-1 px-1 text-lg;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  font-weight: bold;
  height: 1.75rem;
}

.form-item:not(:first-child) {
  margin-top: 1em;
}

.form-item > :not(:first-child) {
  margin-top: 0.25em;
}

.form-item__label {
  display: block;
}

.form-item__element {
  display: inline-block;
  padding: 0.5em;
  vertical-align: middle;
  background: #fff;
  border-radius: 4px;
  border: 1px solid gray;
}

.form-item__element--select {
  -webkit-appearance: none;
  -moz-appearance: none;
  padding-right: 2em;
}

.form-item__element--select:invalid {
  color: gray;
}

.form-item__element--select [disabled] {
  color: gray;
}

.form-item__element--select option {
  color: #444;
}

.select-wrap::after {
  margin-left: -2em;
  font-size: 0.75em;
  pointer-events: none;
  content: '▼';
}
</style>
