<template>
  <div class="card-slim mb-8 shadow-1 flex items-center w-full">
    <img v-if="page.links.image" :src="page.links.image" class="w-48  border-r border-gray-400 px-8">
    <div class="w-full py-2 px-4 pb-6">
      <div class="mt-4 w-full flex justify-between">
        <ul class="flex  ml-4  mb-1 items-center border-b-2 border-gray-200">
          <li v-for="section in sections"
              class="flex  justify-between items-center  pb-3 pr-0 -mb-2px  pl-2 mx-2 text-gray-500 border-b-2 border-transparent"
              :class="{'text-blue-600 font-semibold border-blue-500': section.active === true }"
          >
            <a
                @click.prevent="loadSection(section)"
                class="font-semibold  uppercase mr-1 flex-grow  "
            >
              <span class="text-sm tracking-widest">{{ section.title }}</span>
            </a>
          </li>
        </ul>
        <div class="flex items-center  ml-8 pr-2">
          <a v-if="page.links.view" class="mr-2 hover:text-blue-400" @click="view">
            <sv-icon name="eye" class="bg-gray-200 text-gray-500 rounded py-2 px-3 text-xl"></sv-icon>
          </a>
          <a v-if="page.links.edit" class="mr-2 hover:text-blue-400" @click="edit">
            <sv-icon name="edit" class="bg-gray-200 text-gray-500 rounded py-2 px-3 text-xl"></sv-icon>
          </a>
          <a v-if="page.links.create" class=" hover:text-blue-400 " @click="create">
            <sv-icon name="plus" class="bg-green-500 text-white rounded py-2 px-3 text-xl"></sv-icon>
          </a>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SvIcon from '../components/SvIcon'

export default {
  name: 'SvPageNav',
  components: { SvIcon },
  data() {
    return {
      sections: []
    }
  },
  computed: {
    page() {
      return this.$parent.$props
    },
  },

  created() {
    this.page.sections.map(section => this.sections.push({...section}))
  },
  methods: {
    edit() {
      this.$parent.$data.url = null
      this.$nextTick(() => this.$parent.$data.url = this.page.links.edit)
    },
    view() {
      this.$parent.$data.url = null
      this.$nextTick(() => this.$parent.$data.url = this.page.links.view)
    },
    create() {
      this.$router.push(this.page.links.create)
    },
    loadSection(section) {
      this.$parent.$data.url = null
      this.portal = !!section.portal

      this.$nextTick(() =>  this.$parent.$data.url = section.url)

      this.sections.map(_section => {
          this.$set(_section, 'active', _section.url === section.url)
      })

    },
  }
}
</script>
