<script>
export default {
  name: 'SvRouter',
  data() {
    return {
      current: {
        component: null,
        props: {},
        classes: {}
      },
      url: null
    }
  },
  created() {
    this.$events.listen('route:changed', (newRoute) => this.routeChanged(newRoute))
    if (this.$route) {
      this.activateRoute(this.$route)
    } else {
      alert('you should not be seeing this')
    }
  },

  render(c) {
    if (!this.current) {
      return this._e()
    }
    const current = this.current

    return c(current.component, {
      tag: 'component',
      props: current.props,
      staticClass: current.classes,
    })
  },
  methods: {
    async fetch(url) {
      this.ready = false
      const resp = await this.$api.get(url)
      if (resp.fail) {
        console.log('Cant fetch block', resp)
        return this.$notifyError(resp)
      }

      this.activateComponent(resp.data.component, resp.data.props, resp.data.classes)
      this.url = url
    },

    reload() { this.fetch() },
    routeChanged(newRoute) {
      this.activateRoute(newRoute)
    },
    activateRoute(route) {
      if (route.component === 'SvLoader') {

        if (this.url && this.url.includes('?')) {
          const currentParts = this.url.split('?')
          const nextParts = route.props.url.split('?')

          if (currentParts[0] === nextParts[0]) {
            return this.$debug('Only query changed ')
          }

        }
        // this.$debug('Fetching ' + route.props.url)
        return this.fetch(route.props.url)
      }

      this.activateComponent(route.component, route.props)
    },
    activateComponent(component, props, classes) {
      this.current = null
      this.$nextTick(() => {
        this.current = { component, props, classes }
      })
    }
  },
}
</script>
