<template>
  <thead>
    <tr>
      <th class="w-8" v-if="table.selectable"></th>
      <th class="w-8" v-if="table.showIdColumn">ID</th>
      <th @dblclick="table.reload" v-for="field in table.config.fields" :class="field.classes">
          <a @click.prevent="orderBy(field.name)">{{ field.label }}
            <sv-icon v-if="orderField === field.name" :name="orderDirection === 'ASC' ? 'chevron-up' : 'chevron-down'"></sv-icon>
            <sv-icon v-else class="text-gray-400" name="unfold-more"></sv-icon>
          </a>
      </th>
      <th v-if="table.rowActions.length"></th>
    </tr>
  </thead>

</template>

<script>
import SvIcon from '../SvIcon'
export default {
  name: 'SvTableHead',
  components: { SvIcon },
  inject: ['table'],
  data() {
    return {
      orderField: null,
      orderDirection: null,
    }
  },
  methods: {
    orderBy(fieldName) {
      this.orderField = fieldName
      this.orderDirection = this.orderDirection === 'ASC' ? 'DESC' : 'ASC'
      this.table.orderBy(this.orderField,  this.orderDirection)
    }
  },
}
</script>
