import swal from 'sweetalert2'
import Service from './Service'

export default class Notifier extends Service {

  get confirmer() {
    return () => {
      return swal.fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, delete it!'
      })
    }
  }

  success(message) {
    this.show(message, { position: 'top-end', toast: true, showConfirmButton: false, timer: 3000 })
    // this.$notify.success({ title: 'Success', message })
  }

  show(message, params) {
    const def = {
      type: 'success',
      title: '',
      html: message,
    }
    swal({ ...def, ...params })
    // this.$notify.success({ title: 'Success', message })
  }

  warn(message) {
    swal({
      position: 'top-end',
      type: 'warning',
      toast: true,
      text: message,
      showConfirmButton: false,
      timer: 3000
    })
    // this.$notify.warning({ title: 'Warning', message })
  }

  error(err) {
    let message
    if (typeof err === 'object') {
      if (err.constructor.name === 'ApiResponse') {
        err = err.$error
      }
    }
    if (err.message && !err.response) {
      message = err.message
    } else if (err.response) {
      if (err.response.data) {
        const data = err.response.data
        if (typeof data.errors !== 'undefined') {
          message = Object.values(data.errors).join('<br>')
        } else if (typeof data.error !== 'undefined') {
          if (data.error.description) {
            message = data.error.description
          } else if (data.error.list) {
            message = Object.values(data.error.list).join('<br>\n')
          }
        }
      }
    } else {
      message = err
    }
    // this.$notify.error({
    //   title: 'Error',
    //   message,
    //   dangerouslyUseHTMLString: true,
    // })
    swal({
      type: 'error',
      title: 'Error',
      html: message,
      showConfirmButton: true,
      timer: 5000,
    })

    return message
  }
}
