<template>
  <li :class="{'active': section.active}">
    <template v-if="items">
      <a @click="toggleSection(section)" class="has-sub" :class="section.open ? 'toggled': ''">
        <sv-icon class="" v-if="section.icon" :name="section.icon"></sv-icon>
        <i v-else class="zmdi zmdi-circle-o text-blue-400"></i>
        <span class="uppercase">{{ section.title }}</span>
      </a>

      <sv-slideable :active="section.open">
        <ul class="sv-list-sub">
          <li v-for="subpage in section.sections" :class="{'active': isCurrentUrl(subpage.url)}">
            <a @click.prevent="goTo(section, subpage)"> {{ subpage.title }}</a>
          </li>
        </ul>
      </sv-slideable>
    </template>
    <template v-else>
      <a :href="section.url">
        <i class="zmdi zmdi-copy"></i>
        <span>{{ section.title }}</span>
      </a>
    </template>
  </li>
</template>

<script>
import SvSlideable from '../SvSlideable'
import SvIcon from '../SvIcon'

export default {
  name: 'SvSidebarSection',
  components: { SvIcon, SvSlideable },
  props: ['section', 'toggleSection', 'goTo'],
  methods: {
    isCurrentUrl(url) {
      const isCurrent = '/' + url === this.$route.resolvedPath

      if (isCurrent) {
        this.$set(this.section, 'active', true)
        this.$set(this.section, 'open', true)
      }

      return isCurrent
    }
  },
  computed: {
    items() {
      return this.section.sections
    }
  },
}
</script>

