<template>
  <div>
    <div class="card-body border-b-2" v-for="field in fields">

      <div class="w-full" v-if="field.meta && field.meta.full">
        <sv-field
            :field="field"
            :form="form"
            v-model="field.presenting ? field.value : form.data[field.name]"
        >
        </sv-field>
        <p class="sv-field-helper">{{ hasError(field) ? form.getError(field): ''}}</p>
      </div>

      <div v-else :class="{'error': hasError(field)}" class="flex sv-field-group">
        <div class="field-label">
          <p>{{ field.label }}</p>
          <p v-if="field.hint" class="mt-4 text-sm" style="color: #979ca8;">{{ field.hint }}</p>
        </div>
        <div class="field-input">
          <sv-field
              :field="field"
              :form="form"
              v-model="field.presenting ? field.value : form.data[field.name]"
          >
          </sv-field>
          <p v-if="hasError(field)" class="sv-field-helper">{{ hasError(field) ? form.getError(field): ''}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SvLabel from './fields/SvLabel'
import SvField from './fields/SvField'

export default {
  name: 'SvFormFields',
  components: { SvField, SvLabel },
  props: ['fields', 'form'],
  methods: {
    hasError(field) {
      return this.form && this.form.hasError(field)
    },
  },
}
</script>


<style>
  .sv-field-group .field-label p {
    @apply text-blue-700 font-semibold uppercase tracking-widest;
  }
  .sv-field-group .field-label {
    @apply w-1/3 pr-10;
  }

  .sv-field-group .field-input {
    @apply w-2/3;
  }

  .sv-field-group.error p.sv-field-helper {
    color: #c14544;
  }

</style>
