<template>
    <div class="flex justify-between items-center text-gray-500 p-3">
      <div class="mr-8">Showing {{ pagination.total }} Records</div>

      <ul class="pagination pagination-info">
        <li class="page-item prev-page">
          <a aria-label="Previous"
             :class="{'disabled': !hasPrevious }"
             @click.prevent="previousPage"
             class="page-link">
            <span aria-hidden="true">
                        <sv-icon name="chevron-left" class="text-2xl" :class="{'disabled':!hasPrevious }"></sv-icon>
            </span></a
          ></li>
        <li class="page-item active"><a class="page-link">
          {{ table.currentPage }}
        </a></li>
        <li class="page-item next-page">
          <a aria-label="Next" class="page-link"
             :class="{'disabled':!hasNext }"
             @click.prevent="nextPage"
          ><span aria-hidden="true">
          <sv-icon name="chevron-right" class="text-2xl" :class="{'disabled':!hasNext }"></sv-icon>
          </span></a></li>
      </ul>
    </div>
</template>

<script>
import SvIcon from '../SvIcon'
export default {
  name: 'SvPagination',
  components: { SvIcon },
  inject: ['table'],
  methods: {
    previousPage() {
      this.table.loadPage(this.currentPage - 1)
    },
    nextPage() {
      this.table.loadPage(this.currentPage + 1)
    },
  },
  computed: {
    currentPage() {
      return this.table.currentPage
    },
    pagination() {
      return this.table.pagination
    },
    hasPrevious() {
      return this.currentPage > 1
    },
    hasNext() {
      return this.pagination.last_page > this.table.currentPage
    },
  },
}
</script>

