<template>
  <div class="card">
    <div v-for="field in viewFields" class="card-body border-b-2">
      <div class="flex">
        <div class="w-1/3 pr-10">
          <div class="text-blue-700 font-semibold uppercase tracking-widest">{{ field.label }}</div>
          <p class="mt-4 text-sm text-gray-700">
          </p>
        </div>
        <div class="w-2/3">
          <sv-field :field="field"></sv-field>
        </div>
      </div>
    </div>
    <div class="flex justify-end flex-wrap w-full p-4 bg-gray-100">
      <div v-if="createdAt" class="flex badge green p-0 mx-4">
        <span class="p-1 px-2 bg-green-300 text-white">Created</span>
        <span class="p-1 px-2">{{ createdAt.value}}</span>
        <a v-if="createdBy"
              class="p-1 px-2 bg-green-200 text-gray-600"
              :href="createdBy.meta.link">@ {{ createdBy.value}}</a>
      </div>

      <div v-if="updatedAt" class="flex badge blue p-0 mx-4">
        <span class="p-1 pr-2 bg-blue-300 text-white">Updated</span>
        <span class="p-1 pl-2">{{ updatedAt.value}}</span>
        <a v-if="updatedBy"
           class="p-1 px-2 bg-blue-200 text-gray-600"
           :href="updatedBy.meta.link">@ {{ updatedBy.value}}</a>
      </div>

      <div v-if="deletedAt" class="flex badge red p-0 mx-4">
        <span class="p-1 pr-2 bg-red-300 text-white">Deleted</span>
        <span class="p-1 pl-2">{{ deletedAt.value}}</span>
        <a v-if="deletedBy"
           class="p-1 px-2 bg-red-200 text-gray-600"
           :href="deletedBy.meta.link">@ {{ deletedBy.value}}</a>
      </div>
    </div>
  </div>

</template>

<script>
import SvField from '../components/form/fields/SvField'

export default {
  name: 'SvResourceView',
  components: { SvField },
  props: {
    fields: {},
  },
  data() {
    return {
      // viewing: true,
      // url: null,
      // portal: null,
    }
  },

  created() {

  },
  computed: {

    createdAt() {
      return this.fields['created_at']
    },

    createdBy() {
      const createdBy = this.fields['created_by']
      if (createdBy && createdBy.value) {
        return createdBy
      }
    },

    updatedAt() {
      return this.fields['updated_at']
    },

    updatedBy() {
      const updatedBy = this.fields['updated_by']
      if (updatedBy && updatedBy.value) {
        return updatedBy
      }
    },

    deletedAt() {
      const deletedAtField = this.fields['deleted_at']
      if (!deletedAtField || !deletedAtField.value)
        return null

      return deletedAtField
    },

    deletedBy() {
      const deletedBy = this.fields['deleted_by']
      if (deletedBy && deletedBy.value) {
        return deletedBy
      }
    },

    viewFields() {
      const hidden = ['created_at', 'created_by', 'updated_at', 'updated_by', 'deleted_at', 'deleted_by']
      return Object.keys(this.fields).filter((name) => hidden.indexOf(name) === -1).map((name) => this.fields[name])

    },

  },

  methods: {

  },
}
</script>

